* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.navbar {
  width:100%;
  height:10vh;
  background: black;
  color:white;
  display:flex;
  align-items: center;
  justify-content: space-evenly;
}

.footer {
  width:100%;
  height:10vh;
  background: black;
  color:white;
  display:flex;
  align-items: center;
  justify-content: center;
}

.book-list {
  min-height: 80vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  justify-items: center;
  text-align: center;

}

img {
  width: 250px;
  height: 300px;
  transition: all 0.5s ease;
}

img:hover {
  transform: scale(1.2);
}

.book {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}

button {
  padding: 10px;
  background: black;
  color: white;
  border-radius: 10px;


}